

import Vue from 'vue';
import FastClick from 'fastclick';
import VueRouter from 'vue-router';
import App from './App';
import router from './router/index';
import 'lib-flexible';
import './styles/main.css';
import './assets/iconfont/iconfont';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'


import IconSvg from './components/icon-svg/IconSvg.vue';
import DjCommon from './components/dj-common/index.vue';

Vue.component('icon-svg', IconSvg);
Vue.component(DjCommon.name, DjCommon);
Vue.component('group', Group);
Vue.component('cell', Cell);
Vue.component('cell-box', CellBox);
Vue.use(DatetimePlugin);
Vue.use(VueRouter);

FastClick.attach(document.body);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app');



