
import DjGroup from '../../components/dj-group/DjGroup.vue';
import DjSwitch from '../../components/dj-switch/index.vue';
import { _Date } from '../../common/common';

import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import DjCell from '../../components/dj-cell/index.vue';
import Box from '../../../node_modules/vux/src/components/box/index.vue';
import {
getEventSourceToString,
getEventLevelToString,
getInitEventData
} from './formMethods';

export default {
components: {
Box,
XButton,
XTextarea,
DjCell,
XInput,
DjGroup,
Datetime,
InlineXSwitch,
DjSwitch,
Popup,
Radio,
Icon
},
data() {
return {
commitTime: _Date.getFormatTime('YYYY-MM-DD'),
urgent: '',
event: '',
eventShow: false,
eventLevelIsShow: false,
eventSourceIsShow: false,
baseFramework: 1,
eventLevel: '',
eventSource: '',
eventList: []
};
},
created() {
this.initEventList();
},
methods: {
initEventList() {
var arr = [
{id: '1', name: 'a1', grade: '1'},
{id: '2', name: 'a2', grade: '1'},
{id: '3', name: 'a3', grade: '1'},
{id: '4', pid: '1', name: 'b1', grade: '2'},
{id: '5', pid: '1', name: 'b2', grade: '2'},
{id: '6', pid: '2', name: 'b3', grade: '2'},
{id: '7', pid: '3', name: 'b4', grade: '2'},
{id: '8', pid: '4', name: 'c1', grade: '3'},
{id: '9', pid: '5', name: 'c2', grade: '3'},
{id: '10', pid: '6', name: 'c3', grade: '3'},
{id: '11', pid: '7', name: 'c4', grade: '3'},
{id: '12', pid: '6', name: 'c5', grade: '3'},
{id: '13', pid: '7', name: 'c6', grade: '3'},
{id: '14', pid: '4', name: 'c7', grade: '3'}
];
this.eventList = getInitEventData(arr);
},
change() {

},
onConfirm() {

},
selectEvent() {
this.eventShow = true;
},
selectEventLevel() {
this.eventLevelIsShow = true;
},
selectEventSource() {
this.eventSourceIsShow = true;
},
formCommit() {

},
formEventData(arr, data) {
this.eventList.forEach(item => {
data.id === item.id && item.pid === data.pid ? this.$set(item, 'checked', true) : this.$set(item, 'checked', false);
});
},
resetSelect(arr, data) {
arr.forEach(item => {
data.id === item.id && item.pid === data.pid ? this.$set(item, 'checked', true) : this.$set(item, 'checked', false);
if(item.child && item.child.length > 0) {
this.resetSelect(item.child, data);
}
});
},
eventClick(item) {
if(item.checked) {
return;
}
this.resetSelect(this.eventList, item);
this.event = item.name;
this.eventShow = false;
}
},
watch: {
baseFramework(val) {
this.value = val;
this.popupShow = false;
},
eventLevel(val) {
this.eventLevelIsShow = false;
},
eventSource(val) {
this.eventSourceIsShow = false;
}
},
computed: {
getEventLevel(type) {
return getEventLevelToString(this.eventLevel);
},
getEventSource(type) {
return getEventSourceToString(this.eventSource);
}
}
};



