
import DjGrid from '../../components/dj-grid/DjGrid.vue';
import DjGridItem from '../../components/dj-grid/DjGridItem.vue';
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { sendRequest } from '../../service/fetch';
export default {
name: 'home',
components: {
DjGrid,
DjGridItem
},
data () {
return {
};
},
created() {
this.getInfo({id: 1});
},
methods: {
getInfo(data) {
return new Promise((resolve, reject) => {
let opts = {
url: '/dj/portal/getInfo',
data: data || {}
};
let callback = {
httpStart(request) {

},
httpComplete(response, request) {
resolve(response);
},
httpError(error, request) {
reject();
}

};
sendRequest(callback, opts);
});
}
}
};



