function getEventLevelToString(type) {
    var str = '';
    switch (type) {
        case 1:
            str = '低级';
            break;
        case 2:
            str = '中级';
            break;
        case 3:
            str = '高级';
            break;
        default:
            str = '';
            break;
    }
    return str;
}
function getEventSourceToString(type) {
    var str = '';
    switch (type) {
        case 1:
            str = '电话';
            break;
        case 2:
            str = '邮件';
            break;
        case 3:
            str = '口述';
            break;
        case 4:
            str = 'QQ';
            break;
        case 5:
            str = 'OA';
            break;
        default:
            str = '';
            break;
    }
    return str;
}
function getInitEventData(arr) {
    var eventObj = [];
    arr.forEach(function (item) {
        var data = {};
        switch (item.grade) {
            case '1':
                data.sjlba = item.id;
                data.sjlba_mc = item.name;
                break;
            case '2':
                data.sjlbb = item.id;
                data.sjlbb_mc = item.name;
                item.data = data;
                break;
            case '3':
                data.sjlbc = item.id;
                data.sjlbc_mc = item.name;
                break;
            default:
                break;
        }
        item.data = data;
    });
    arr.forEach(function (item) {
        if(item.grade === '1') {
            eventObj.push(item);
        }
    });
    arr.forEach(function (item) {
        if(item.grade === '2') {
            eventObj.forEach(function (item2) {
                if(item.pid === item2.id) {
                    item2.child = item2.child || [];
                    Object.assign(item.data, item2.data);
                    item2.child.push(item);
                }
            });
        }
    });
    arr.forEach(function (item) {
        if(item.grade === '3') {
            eventObj.forEach(function (item2) {
                if(item2.child && item2.child.length > 0) {
                    item2.child.forEach(function (item3) {
                        if(item.pid === item3.id) {
                            item3.child = item3.child || [];
                            Object.assign(item.data, item3.data);
                            item3.child.push(item);
                        }
                    });
                }
            });
        }
    });
    return eventObj;
}
export {
    getEventLevelToString,
    getEventSourceToString,
    getInitEventData
};



