export default {
    name: 'dj-cell',
    props: {
        title: {
            type: String
        },
        needIcon: {
            type: Boolean,
            default: true
        },
        value: {
            type: String
        },
        placeholder: {
            type: String
        }
    },
    methods: {
        cellNodeClick() {
            this.$emit('cellNodeClick');
        },
        cellClick() {
            this.$emit('cellClick');
        }
    }
};



