import DjGroup from '../../components/dj-group/DjGroup.vue';
import DjCellContent from '../pslinfo/DjCellContent.vue';
import { _Date as dataTime } from '../../common/common';
export default {
    components: {
        DjCellContent,
        DjGroup
    },
    data() {
        return {
            dataList: [
                {title: "基本薪资", cont: "5000元"},
                {title: "职位薪资", cont: "200元"},
                {title: "加班薪资", cont: "500元"}
            ],
            selectTime: dataTime.getFormatTime('YYYY年MM月'),
            originTime: dataTime.getFormatTime('YYYY-MM')
        };
    },
    methods: {
        selectSalaryTime() {
            var _this = this;
            this.$vux.datetime.show({
                cancelText: '取消',
                confirmText: '确定',
                format: 'YYYY-MM',
                value: _this.originTime,
                onConfirm(val) {
                    _this.selectTime = _this.getFormVal(val);
                    _this.originTime = val;
                },
                onShow() {

                },
                onHide() {

                }
            });
        },
        getFormVal(val) {
            let arr = val.split('-');
            return `${arr[0]}年${arr[1]}月`;
        }
    }
};



