export let _Date = {
    getCrtTime(t = null) {
        return t === null ? new Date() : new Date(t);
    },
    getYear(t = null) {
        let date = this.getCrtTime(t);
        return date.getFullYear();
    },
    getMonth(t = null) {
        let date = this.getCrtTime(t);
        return date.getMonth() + 1;
    },
    getDate(t = null) {
        let date = this.getCrtTime(t);
        return date.getDay();
    },
    getSeconds(t = null) {
        let date = this.getCrtTime(t);
        return date.getSeconds();
    },
    getMinutes(t = null) {
        let date = this.getCrtTime(t);
        return date.getMinutes();
    },
    getHours(t = null) {
        let date = this.getCrtTime(t);
        return date.getHours();
    },
    getFormatTime(f, t = null) {
        let Y = this.getYear(t);
        let M = this.getMonth(t);
        let D = this.getDate(t);
        let H = this.getHours(t);
        let m = this.getMinutes(t);
        let S = this.getSeconds(t);
        M = this.getLeter10(M);
        D = this.getLeter10(D);
        H = this.getLeter10(H);
        m = this.getLeter10(m);
        S = this.getLeter10(S);
        return f.replace('YYYY', Y).replace('MM', M).replace('DD', D).replace('HH', H).replace('mm', m).replace('SS', S);
    },
    getLeter10(t) {
        return t < 10 ? '0' + t : t;
    }
};



