import Vue from 'vue';
import Router from 'vue-router';
import home from '../view/home/home.vue';
import AutoService from '../view/autoservice/AutoService.vue';
import PslInfo from '../view/pslinfo/PslInfo.vue';
import Salary from '../view/salary/Salary.vue';
import Holiday from '../view/holiday/Holiday.vue';
import WorkTime from '../view/worktime/WorkTime.vue';
import From from '../view/form/From.vue';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/home',
            name: 'home',
            component: home
        },
        {
            path: '/',
            redirect: '/home'
        },
        {
            path: '/autoService',
            name: 'autoService',
            component: AutoService
        },
        {
            path: '/pslInfo',
            name: 'pslInfo',
            component: PslInfo
        },
        {
            path: '/salary',
            name: 'salary',
            component: Salary
        },
        {
            path: '/holiday',
            name: 'holiday',
            component: Holiday
        },
        {
            path: '/workTime',
            name: 'workTime',
            component: WorkTime
        },
        {
            path: '/formCommit',
            name: 'formCommit',
            component: From
        }
    ]
});



